@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  color: "#013237";
  padding: 0;
  margin: 0;
}

.navIcon { fill: orange; }

h1, h2, h3, p {
  color: "#013237";
  line-height: 150%;
}

.sm { font-size: 12px; }
.md { font-size: 14px; }
.lg { font-size: 1rem; }
.hs { font-size: 20px; }
.hp { font-size: 1.5rem; }

.regular { font-weight: 400; }
.medium { font-weight: 500; }
.semi-bold { font-weight: 600; }

.secondary-gray { color: "#A19CB9";  }
.text-white { color: "#FFF"; }
.text-green { color: "4CA771"; }
.nav-non { color: "#C9D6C7"; }

.text-head {
  font-size: 1rem;
  font-weight: 600;
  margin: 24px 0 20px 0;
  color: "#013237";
}

.text-gradient {
  background: linear-gradient(to right, #11998e, #38ef7d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.bg-gradient-blue {
  background: linear-gradient(to right, #92FE9D, #00C9FF);
}
.bg-gradient-green {
  background: linear-gradient(to right, #11998e, #38ef7d);
}
.bg-mesh {
  background-image: url("assets/mesh.jpg");
  background-size: cover;
  background-position: center;
}
.bg-layer-green {
  background: linear-gradient(to top, #C0E6BA, rgba(0, 0, 0, 0));
}

.bg-white { background: #fff; }
.bg-gradient-white {
  background: linear-gradient(to top, #FFF, #FFF, rgba(0, 0, 0, 0));
}

.nav-fill {
  fill: #C9D6C7;
}
.nav-fill-active {
  fill: #4CA771;
}
.nav-color {
  color: #C9D6C7;
}
.nav-color-active {
  color: #4CA771;
}

.flex { display: flex; }
.grid { display: grid; }

.flex-row {
  flex-direction: row;
}
.flex-col {
  flex-direction: column;
}

.place-items-center {
  place-items: center;
}

.justify-center { justify-content: center; }
.justify-around { justify-content: space-around; }
.justify-between { justify-content: space-between; }
.justify-evenly { justify-content: space-evenly; }

.items-center { align-items: center; }
.items-start { align-items: flex-start; }
.items-end { align-items: flex-end; }
.items-stretch { align-items: stretch; }

.grid-cols-2 {
  grid-template-columns: repeat(2, minmax(0, 1fr));
}
.grid-cols-3 {
  grid-template-columns: repeat(3, minmax(0, 1fr));
}
.grid-cols-4 {
  grid-template-columns: repeat(4, minmax(0, 1fr));
}
.grid-cols-5 {
  grid-template-columns: repeat(5, minmax(0, 1fr));
}

.auto-rows-auto {
  grid-auto-rows: auto;
}
.auto-cols-auto {
  grid-auto-columns: auto;
}

.card {
  border-radius: 12px;
  background-color: "#FFF";
  box-shadow: 0 4px 16px rgba(46, 130, 80, 0.12);
}


/* Switch Style */
.switch {
  display: inline-block;
  height: 24px;
  position: relative;
  width: 48px;
}

.switch input {
  display:none;
}

.slider {
  background-color: #ccc;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: .4s;
}

.slider:before {
  background-color: #fff;
  bottom: 2px;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  position: absolute;
  transition: .4s;
}

input:checked + .slider {
  background-color: #4CA771;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
/* Done */
